/* ------ Media Queries ------ */
/*------1201px  -  1366px-------*/
@media screen and (max-width: 1500px) {
  .header__menu li a {
    font-size: 16px;
  }
  .offer {
    padding-top: 50px;
  }
  .product {
    padding: 20px 0 50px;
  }
  .products__title {
    margin: 50px 0;
  }
}

/* 992-1200px */
@media screen and (max-width: 1200px) {
  .header__menu li {
    margin-right: 20px;
  }
  .header__menu li:not(:last-child) {
    margin-right: 30px;
  }
  .header .work-time {
    display: none;
  }
  .header__phone {
    font-size: 20px;
  }
  .offer {
    padding-top: 20px;
  }
  .offer__container {
    flex-flow: column-reverse;
    align-items: center;
  }
  .product {
    padding: 20px 0 50px;
  }
  .product__wrap {
    flex-flow: column;
  }
  .production__card:nth-child(3) {
    grid-column: 1 / 2;
    grid-row: 4 / 5;
    width: 286px;
    max-width: 286px;
  }
  .production__card:nth-child(4) {
    grid-row: 4/5;
    justify-self: flex-end;
  }
  .production__card:nth-child(6) {
    grid-column: 1/2;
    grid-row: 5/6;
    margin-left: 0;
  }
  .production__card:nth-child(7) {
    justify-self: center;
  }
  .contact__img {
    display: none;
  }
  .contacts__map iframe {
    width: 530px;
  }
}

/* 768-992px */
@media screen and (max-width: 992px) {
  .header .gamburger {
    display: block;
  }
  .header__wrap-bottom {
    position: relative;
  }
  .header nav {
    overflow: hidden;
  }
  .header__menu {
    display: none;
    flex-direction: column;
    justify-content: center;
    align-items: center;
    width: 100%;
    padding: 20px 0 20px 15px;
    position: absolute;
    left: 0;
    margin-top: 33px;
    z-index: 999;
  }
  .header__menu li {
    margin-right: 0;
    margin-top: 25px;
  }
  .production__wrap {
    display: flex;
    flex-flow: column;
    align-items: center;
  }
  .production__card:nth-child(2) {
    width: 350px;
    max-width: 350px;
    max-height: 300px;
    height: 300px;
    background-position: top right;
  }
  .production__card:nth-child(3) {
    width: 350px;
    max-width: 350px;
  }
  .production__card:nth-child(5) {
    width: 350px;
    max-width: 350px;
  }
  .production__card:nth-child(6) {
    width: 350px;
    max-width: 350px;
  }
  .footer-wrap {
    justify-content: space-around;
  }
  .footer-wrap__menu {
    display: none;
  }
  .footer-rights {
    margin-top: 50px;
  }
  .contacts__wrap {
    flex-flow: column;
    align-items: center;
  }
  .contacts__map iframe {
    margin-top: 30px;
    width: 100%;
  }
  .tabs__content .mobile {
    width: 100%;
    overflow-x: auto;
    -webkit-overflow-scrolling: touch;
  }
}

/* 576-768px */
@media screen and (max-width: 768px) {
  .header__wrap-top span {
    display: none;
  }
  .header__btn {
    width: 100%;
  }
  .box-modal {
    width: 100%;
  }
  .modal-form {
    width: 100%;
  }
  .form {
    padding: 10px;
  }
  .form__title {
    font-size: 30px;
  }
  .form__text {
    font-size: 16px;
    width: 100%;
