  .swiper-button-next, .swiper-container-rtl .swiper-button-prev {
    display: none;
  }
  .swiper-button-prev, .swiper-container-rtl .swiper-button-next {
    display: none;
  }
  .footer .work-time {
    margin: 30px 0;
  }
  .footer-wrap {
    flex-flow: column;
  }
  .footer-wrap__contacts-box {
    text-align: left;
  }
}

